'use client';

import styled from 'styled-components';
import { motion } from 'framer-motion';
import { Mail, Phone, MapPin, Linkedin, Github } from 'lucide-react';
import { Container, Section } from '@/components/ui/Container';
import { Navbar } from '@/components/layout/Navbar';
import { Footer } from '@/components/layout/Footer';

const ContactGrid = styled.div`
  display: grid;
  grid-template-columns: 1fr 1.5fr;
  gap: 4rem;
  padding-top: 8rem;
  @media (max-width: 968px) {
    grid-template-columns: 1fr;
    gap: 3rem;
  }
`;

const ContactInfo = styled.div`
  h1 {
    font-size: 3rem;
    margin-bottom: 1.5rem;
  }
  p {
    color: var(--secondary);
    margin-bottom: 2.5rem;
    font-size: 1.1rem;
  }
`;

const InfoItem = styled.div`
  display: flex;
  align-items: center;
  gap: 1.5rem;
  margin-bottom: 2rem;
  .icon {
    width: 60px;
    height: 60px;
    background: var(--accent);
    color: var(--primary);
    border-radius: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .text {
    h4 { margin-bottom: 0.25rem; }
    p { margin: 0; color: var(--secondary); font-size: 0.95rem; }
  }
`;

const ContactForm = styled.form`
  background: white;
  padding: 3rem;
  border-radius: 2rem;
  box-shadow: var(--shadows-lg);
  border: 1px solid var(--border);
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
`;

const Input = styled.input`
  width: 100%;
  padding: 1rem 1.5rem;
  border-radius: 0.75rem;
  border: 1px solid var(--border);
  outline: none;
  font-family: inherit;
  &:focus {
    border-color: var(--primary);
  }
`;

const Textarea = styled.textarea`
  width: 100%;
  padding: 1rem 1.5rem;
  border-radius: 0.75rem;
  border: 1px solid var(--border);
  outline: none;
  font-family: inherit;
  min-height: 150px;
  &:focus {
    border-color: var(--primary);
  }
`;

const SubmitBtn = styled.button`
  background: var(--primary);
  color: white;
  padding: 1rem;
  border-radius: 0.75rem;
  font-weight: 700;
  transition: transform 0.2s;
  &:hover {
    transform: translateY(-2px);
    background: #1d4ed8;
  }
`;

export default function ContactPage() {
    return (
        <>
            <Navbar />
            <Section>
                <Container>
                    <ContactGrid>
                        <ContactInfo>
                            <motion.div
                                initial={{ opacity: 0, x: -50 }}
                                animate={{ opacity: 1, x: 0 }}
                            >
                                <h1>Get in Touch</h1>
                                <p>Have a project in mind? Let's talk about how we can build something amazing together.</p>

                                <InfoItem>
                                    <div className="icon"><Mail size={24} /></div>
                                    <div className="text">
                                        <h4>Email</h4>
                                        <p>saleem.kudra@example.com</p>
                                    </div>
                                </InfoItem>

                                <InfoItem>
                                    <div className="icon"><Linkedin size={24} /></div>
                                    <div className="text">
                                        <h4>LinkedIn</h4>
                                        <p>linkedin.com/in/saleemkudra</p>
                                    </div>
                                </InfoItem>

                                <InfoItem>
                                    <div className="icon"><Github size={24} /></div>
                                    <div className="text">
                                        <h4>GitHub</h4>
                                        <p>github.com/saleemkudra</p>
                                    </div>
                                </InfoItem>
                            </motion.div>
                        </ContactInfo>

                        <ContactForm
                            as={motion.form}
                            initial={{ opacity: 0, y: 50 }}
                            animate={{ opacity: 1, y: 0 }}
                        >
                            <div>
                                <label style={{ display: 'block', marginBottom: '0.5rem', fontWeight: 600 }}>Name</label>
                                <Input type="text" placeholder="Your Name" />
                            </div>
                            <div>
                                <label style={{ display: 'block', marginBottom: '0.5rem', fontWeight: 600 }}>Email</label>
                                <Input type="email" placeholder="your@email.com" />
                            </div>
                            <div>
                                <label style={{ display: 'block', marginBottom: '0.5rem', fontWeight: 600 }}>Message</label>
                                <Textarea placeholder="Tell me about your project..." />
                            </div>
                            <SubmitBtn type="submit">Send Message</SubmitBtn>
                        </ContactForm>
                    </ContactGrid>
                </Container>
            </Section>
            <Footer />
        </>
    );
}
